<template>
  <div class="search">
    <el-form
      ref="searchTemplateForm"
      label-position="left"
      label-width="76px"
      :model="formData"
      class="search__form"
    >
      <el-row :gutter="20">
        <el-col :span="4">
          <el-form-item label="套餐编号" prop="id">
            <el-input
              v-model.number="formData.no"
              placeholder="请输入套餐编号"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="套餐名称">
            <el-input
              v-model="formData.title"
              placeholder="请输入套餐名称"
              size="small"
              clearable
            >
            </el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="现价区间" label-width="90px">
            <el-row type="flex">
              <el-col :span="11">
                <el-input
                  v-model.number="formData.price_start"
                  placeholder="最低价"
                  size="small"
                  clearable
                />
              </el-col>
              <el-col :span="2">
                <span style="margin-left:6px">至</span>
              </el-col>
              <el-col :span="11">
                <el-input
                  v-model.number="formData.price_end"
                  placeholder="最高价"
                  size="small"
                  clearable
                />
              </el-col>
            </el-row>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="状态">
            <el-select
              v-model="formData.status"
              placeholder="请选择套餐状态"
              size="small"
              clearable
            >
              <el-option
                v-for="item in selectOptsMaps.saleStatusTypes"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="类型">
            <el-select
              v-model="formData.department"
              placeholder="请选择套餐类型"
              size="small"
              clearable
            >
              <el-option
                v-for="item in getDepartment('', 'arr')"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="40">
        <el-col :span="4">
          <ConsultType
            v-model="formData.ask_mode"
            placeholder="请选择咨询方式"
            label="咨询方式"
          ></ConsultType>
        </el-col>
        <el-col :span="8">
          <el-form-item label="创建日期">
            <el-date-picker
              v-model="formData.createTime"
              type="datetimerange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              prefix-icon="el-icon-date"
              :default-time="['12:00:00']"
              size="small"
              clearable
            >
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="首次上架时间" label-width="100px">
            <el-date-picker
              v-model="formData.firstOnlineTime"
              type="datetimerange"
              prefix-icon="el-icon-date"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['12:00:00']"
              size="small"
              clearable
            />
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" size="small" @click="handleSearch"
            >搜索</el-button
          >
          <el-button type="primary" plain size="small" @click="handleReset"
            >重置</el-button
          >
        </el-col>
      </el-row>
      <el-row :gutter="40"> </el-row>
    </el-form>
  </div>
</template>
<script>
import ConsultType from '@/components/Select/consultType'
import { getDepartment } from '@/utils/dictionary'
const defaultformData = {
  no: '',
  title: '',
  ask_mode: '',
  status: '',
  createTime: [],
  firstOnlineTime: [],
  price_start: '',
  price_end: ''
}

export default {
  props: {
    localFormData: {
      type: Object,
      default: () => {}
    },
    selectOptsMaps: {
      type: Object,
      required: false,
      // 套餐名称name 服务周期duration 定价price 创建时间 创建人create_user 首次上架时间 状态
      default: () => ({
        nameTypes: [],
        durationTypes: [],
        createUserTypes: [],
        priceType: [],
        saleStatusTypes: [
          {
            label: '上架',
            value: 1
          },
          {
            label: '未上架',
            value: 0
          }
        ]
      })
    }
  },
  components: {
    ConsultType
  },
  data() {
    var checkId = (rule, value, callback) => {
      setTimeout(() => {
        if (value < 0) {
          callback(new Error('编号必须大于0'))
        } else {
          callback()
        }
      }, 1000)
    }
    return {
      formData: {},
      searchTemplateForm: {
        id: [{ validator: checkId, trigger: 'blur' }]
      }
    }
  },
  watch: {},
  created() {
    this.formData = Object.assign({}, defaultformData, this.localFormData)
  },
  methods: {
    getDepartment,
    handleSearch() {
      this.$refs['searchTemplateForm'].validate(valid => {
        if (valid) {
          const params = {}
          if (this.formData.no) {
            params.no = this.formData.no
          }
          if (this.formData.title) {
            params.title = this.formData.title
          }
          if (this.formData.ask_mode) {
            params.ask_mode = this.formData.ask_mode
          }
          if (this.formData.department) {
            params.department = this.formData.department
          }
          if (this.formData.price_start !== '') {
            params.price_start = this.formData.price_start * 100
          }
          if (this.formData.price_end !== '') {
            params.price_end = this.formData.price_end * 100
          }
          if (this.formData.status !== '') {
            params.status = this.formData.status
          }
          if (this.formData.createTime && this.formData.createTime.length > 0) {
            params.created_time_start = this.formData.createTime[0]
            params.created_time_end = this.formData.createTime[1]
          }
          if (
            this.formData.firstOnlineTime &&
            this.formData.firstOnlineTime.length > 0
          ) {
            params.first_online_start = this.formData.firstOnlineTime[0]
            params.first_online_end = this.formData.firstOnlineTime[1]
          }
          this.$emit('search', { searchParams: params })
        } else {
          return false
        }
      })
    },
    handleReset() {
      this.formData = Object.assign({}, defaultformData)
      this.handleSearch()
    }
  }
}
</script>

<style lang="scss">
.search {
  &__form {
    margin-bottom: -20px;
    .el-form-item {
      margin-bottom: 20px;
      &__label,
      &__content {
        height: 32px;
        line-height: 32px;
      }
    }
    .el-date-editor,
    .el-select {
      width: 100%;
    }
  }
}
</style>

<style lang="scss" scoped>
.search {
  &__form {
    &__source {
      display: flex;
      align-items: center;
    }
    &__source-scene,
    &__source-custom-value {
      width: 50%;
    }
    &__source-scene {
      padding-right: 14px;
    }
    &__source-custom-value {
      padding-left: 14px;
    }
  }
}
</style>
