<template>
  <div class="order-list">
    <div class="order-list__search">
      <el-button
        class="order-list__search__collapse-btn"
        type="text"
        @click="searchFormShow = !searchFormShow"
      >
        {{ searchFormShowText }}
        <i
          class="el-icon--right"
          :class="searchFormShow ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
        />
      </el-button>
      <el-collapse-transition>
        <search-form
          ref="searchForm"
          v-show="searchFormShow"
          :cur-order-status="curTabName === 'all' ? '' : curTabName"
          :local-form-data="LocalFormData"
          @search="handleSearch"
        />
      </el-collapse-transition>
    </div>
    <div class="add">
      <el-row>
        <el-button type="primary" size="mini" @click="AddMeal">
          新增套餐
        </el-button>
        <el-button
          type="text"
          class="float-right"
          @click="historyVersion('all')"
        >
          查看套餐历史版本
        </el-button>
      </el-row>
    </div>
    <div class="adminuser-list__body">
      <div slot="header" class="title">套餐列表</div>
      <el-table
        :header-row-style="getHeadStyle"
        :data="tableData"
        border
        :default-sort="{ prop: 'in_service_user_num' }"
        empty-text="暂无数据，请尝试输入其他关键字"
        style="width: 100%;"
      >
        <el-table-column
          align="center"
          fixed
          prop="no"
          label="套餐编号"
          width="100"
          style="text-align: center;"
        />
        <el-table-column
          align="center"
          fixed
          prop="title"
          label="套餐名称"
          width="120"
          style="text-align: center;"
        >
          <template slot-scope="scope">{{
            scope.row.title + scope.row.sub_title
          }}</template>
        </el-table-column>
        <el-table-column
          align="center"
          fixed
          prop="title"
          label="套餐类型"
          width="120"
          style="text-align: center;"
        >
          <template slot-scope="scope">
            {{ getDepartment(scope.row.department, '') }}
          </template>
        </el-table-column>
        <el-table-column label="服务周期" align="center" min-width="80">
          <template slot-scope="scope">
            <span> {{ parseDuration(scope.row.duration) }}个月 </span>
          </template>
        </el-table-column>
        <el-table-column
          label="咨询方式"
          align="center"
          min-width="100"
          prop="ask_mode"
        >
          <template slot-scope="scope">
            {{ scope.row.ask_mode | productType }}
          </template>
        </el-table-column>
        <el-table-column align="center" prop="price" min-width="100">
          <template slot="header">
            <span>现价（元）</span>
            <el-tooltip
              class="item"
              effect="dark"
              placement="top"
              :content="
                '购买套餐时，实际购买价格以此优惠价进行计算，原价仅做展示'
              "
            >
              <i class="el-icon-info"></i>
            </el-tooltip>
          </template>
          <template slot-scope="scope">
            {{ scope.row.price | fenToYuan }}
          </template>
        </el-table-column>
        <el-table-column align="center" min-width="110">
          <template slot="header">
            <span>签约订单情况</span>
            <el-tooltip
              class="item"
              effect="dark"
              placement="top"
              :content="'服务中订单/待服务订单/所有订单数'"
            >
              <i class="el-icon-info"></i>
            </el-tooltip>
          </template>
          <template slot-scope="{ row }">
            {{ row.contract_counts.enabled }}/{{
              row.contract_counts.waiting
            }}/{{ row.contract_counts.total }}
          </template>
        </el-table-column>

        <el-table-column
          align="center"
          prop="create_user"
          label="创建人"
          min-width="80"
        >
          <template slot-scope="{ row }">
            {{ row.creator.name }}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="first_online_at"
          label="首次上架时间"
          min-width="140"
        >
          <template slot-scope="{ row }">
            <span>
              <span v-if="row.first_online_at">
                {{ new Date(row.first_online_at) | formatTime() }}
              </span>
              <span v-else>未上架</span>
            </span>
          </template>
        </el-table-column>
        <el-table-column label="状态" align="center" min-width="100">
          <template slot-scope="scope">
            <p>
              {{ scope.row.status ? '已上架' : '已下架' }}
            </p>
            <p
              v-if="scope.row.status"
              style="color:#f8747f;cursor:pointer;"
              @click="standUpAndDown(scope.row)"
            >
              立即下架
            </p>
            <p
              v-else
              style="color:#409EFF;cursor:pointer;"
              @click="standUpAndDown(scope.row)"
            >
              立即上架
            </p>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="created_at"
          label="创建时间"
          min-width="140"
          style="text-align: center;"
        >
          <template slot-scope="scope">
            {{ new Date(scope.row.first_created_at) | formatTime() }}
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          prop="version"
          label="当前版本"
          min-width="130"
          style="text-align: center;"
        >
          <template slot-scope="scope">
            <p>
              {{ scope.row.version }}
            </p>
            <el-button type="text" @click="historyVersion(scope.row.batch_id)">
              历史版本
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          align="center"
          fixed="right"
          label="操作"
          min-width="120"
        >
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="handleClickEdit(scope.row)"
            >
              编辑</el-button
            >
          </template>
        </el-table-column>
        <template slot="empty">
          <div class="table-empty">
            暂无数据，请尝试输入其他关键字
          </div>
        </template>
      </el-table>
      <div class="adminuser-list__body__pagination">
        <el-pagination
          :current-page="page.currentPage"
          :page-sizes="pageSizes"
          :page-size="page.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="page.total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="putawayDialogVisible"
      width="30%"
      center
    >
      <span>{{ dialogContent }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="putawayDialogVisible = false">取 消</el-button>
        <el-button
          v-if="!!same_name_service_id"
          type="primary"
          @click="replaceSaleFun"
          >{{ dialogConfirmBtnTxt }}</el-button
        >
        <el-button v-else type="primary" @click="confirmShangjia">{{
          dialogConfirmBtnTxt
        }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { getList, changeSaleStatus } from '@/api/FdServiceType'
import SearchForm from './SearchForm'
import moment from 'moment'
import { getDepartment } from '@/utils/dictionary'

// import _ from 'lodash'
export default {
  components: { SearchForm },
  data() {
    return {
      searchVal: {},
      dialogTitle: '上架提示',
      dialogConfirmBtnTxt: '确认上架',
      dialogContent: '',
      putawayDialogVisible: false,
      LocalFormData: {},
      selectOptsMaps: {},
      curTabName: 'all',
      searchFormShow: true,
      page: {
        total: 0,
        pageSize: 10,
        currentPage: 1
      },
      pageSizes: [10, 50, 100],
      tableData: [],
      same_name_service_id: '',
      activeId: '',
      changedStatus: ''
    }
  },
  computed: {
    searchFormShowText() {
      return this.searchFormShow ? '收起' : '展开搜索条件'
    }
  },
  created() {
    this.handlerGetList()
  },
  beforeRouteLeave(to, from, next) {
    if (to.name === 'FdServiceEdit') {
      to.meta.title = '编辑套餐'
    }
    next()
  },
  methods: {
    getDepartment,
    parseDuration(time) {
      return Math.round(moment.duration(time).asMonths())
    },
    historyVersion(batchId) {
      // 查看历史版本
      this.$router.push({
        path: '/fd-setting/FdServiceType/versionHistory',
        query: {
          id: batchId
        }
      })
    },
    // 上下架
    standUpAndDown(row, type) {
      this.putawayDialogVisible = true
      this.activeId = row._id
      this.changedStatus = !row.status
      if (!row.status) {
        // 上架
        this.changedStatus = true
        this.dialogTitle = '上架提示'
        this.dialogContent =
          '上架后，' + row.title + '将进入线上销售，确认上架吗？'
        this.dialogConfirmBtnTxt = '确认上架'
      } else {
        this.changedStatus = false
        this.dialogTitle = '下架提示'
        this.dialogContent = '下架后，' + row.title + '将不可销售，确认下架吗？'
        this.dialogConfirmBtnTxt = '确认下架'
      }
    },
    confirmShangjia() {
      const params = {
        status: this.changedStatus
      }
      if (!this.activeId) {
        return
      }
      changeSaleStatus(this.activeId, params).then(res => {
        const msg = this.changedStatus ? '上架' : '下架'
        this.$message.success(msg + '成功')
        this.putawayDialogVisible = false
        this.handlerGetList(this.$refs.searchForm.formData)
      })
    },
    AddMeal() {
      this.$router.push({ name: 'FdServiceAdd' })
    },
    handleSearch(val) {
      this.searchVal = val.searchParams
      this.page.pageSize = 10
      this.page.currentPage = 1
      this.handlerGetList(this.searchVal)
    },
    handlerGetList(query = {}) {
      const _query = { ...query }
      _query.pageSize = this.page.pageSize
      _query.currentPage = this.page.currentPage
      getList(_query).then(result => {
        this.tableData = result.data.list
        this.page.total = result.data.page.total
      })
    },
    async handleCurrentChange(page) {
      this.page.currentPage = page
      this.handlerGetList(this.searchVal)
    },

    async handleSizeChange(pageSize) {
      this.page.currentPage = 1
      this.page.pageSize = pageSize
      this.handlerGetList(this.searchVal)
    },
    getHeadStyle() {
      return {
        fontSize: '14px',
        color: '#17233D',
        letterSpacing: 0
      }
    },
    handlerClickHistory(row) {
      // 历史
      this.$router.push({
        path: `/medical-service/FdServiceType/${
          row._id
        }/editHistory?name=${encodeURIComponent(row.name)}&id=${
          row._id
        }&duration=${row.duration}&price=${row.price}`
      })
    },
    handleClickEdit(row) {
      // 编辑
      this.$router.push({
        name: 'FdServiceEdit',
        params: {
          id: row._id,
          type: 'update'
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import '~@/styles/mixin.scss';
@import '~@/styles/variables.scss';
.is_old_style {
  margin-left: 3px !important;
}
.order-list {
  .title {
    padding: 10px;
    line-height: 18px;
    font-size: 18px;
    font-family: 'PingFangSC-Medium';
    color: #17233d;
    border: 1px solid #ebeef5;
    border-bottom: none;
  }
  .add {
    margin-bottom: 20px;
  }
  .el-badge__content {
    background: #f8747f;
    &.is-fixed {
      transform: translateY(-30%) translateX(100%) scale(0.7);
    }
  }
  .adminuser-list__body__pagination {
    padding: 30px 0 10px;
    text-align: center;
  }
  .el-tabs {
    &__header {
      margin-bottom: 0;
    }
    &__nav-wrap {
      padding-left: 30px;
      &::after {
        height: 1px;
      }
    }
    &__item {
      padding: 5px 16px;
      height: 50px;
      &.is-active {
        .el-button--text {
          color: #{$activeColor};
        }
      }
      .el-button--text {
        font-size: 14px;
        color: #{$textLightBlock};
        &.order-list__body__button--pending {
          color: #f8747f;
        }
      }
      .el-badge {
        padding: 0 16px;
        &__content {
          background: #f8747f;
          &.is-fixed {
            transform: translateY(10%) translateX(50%) scale(0.7);
          }
        }
      }
    }
  }
  .el-dialog__body {
    line-height: 20px;
  }
  &__body {
    &__table {
      &__trade-Status,
      &__source,
      &__user,
      &__after-sale-status {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        line-height: 1;
        flex-direction: column;
      }
      &__trade-Status {
        min-height: 70px;
        &__close-btn {
          margin-left: 0;
        }
        .el-button--text {
          padding-top: 5px;
          padding-bottom: 0;
        }
      }
      &__source__weapp-scene,
      &__source__weapp-scene:hover {
        text-decoration: underline;
      }
      &__source__custom-scene,
      &__user__see-btn {
        padding-top: 5px;
        transform: scale(0.84);
      }
      &__user__see-btn {
        padding-bottom: 0;
        font-size: 14px;
        color: #{$textLightBlock};
      }
    }
  }
}
</style>

<style lang="scss" scoped>
@import '~@/styles/variables.scss';

.order-list {
  .cherry {
    color: #f8747f;
  }
  &__search {
    position: relative;
    padding-bottom: 30px;
    &__collapse-btn {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 99;
    }
  }
  &__body {
    border: 1px solid #ebeef5;
    border-radius: 4px;
    &__pagination {
      padding: 50px 0 50px;
      text-align: center;
    }
    &__checkbox-hide {
      padding: 30px 20px 20px;
      color: #409eff;
    }
  }
  &__content {
    padding: 0 20px;
  }
}
</style>
