<template>
  <el-form-item :label="label" :prop="prop" :rules="rules">
    <el-select
      :value="value"
      :placeholder="placeholder"
      @change="onValueChange"
      clearable
      :size="size"
      :disabled="disabled"
    >
      <el-option
        v-for="item in $store.state.dictionary.productTypes"
        :key="item.value"
        :value="item.value"
        :label="item.label"
        >{{ item.label }}</el-option
      >
    </el-select>
  </el-form-item>
</template>
<script>
export default {
  props: {
    size: {
      type: String,
      default: 'small'
    },
    label: {
      type: String,
      default: '咨询方式'
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    value: [String, Boolean],
    moduleName: {
      type: String,
      default: ''
    },
    prop: {
      type: String,
      default: ''
    },
    rules: {
      type: Array,
      default: () => []
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onValueChange(val) {
      this.$emit('input', val)
    }
  }
}
</script>
